.login-box__description {
    color: var(--lightgrey);
    text-align: center;
}

.login-redirect {
    color: var(--verylightgrey);
    font-size: 0.75em;
}

.login-box {
    flex-direction: row !important;
}

.login-right-box {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1em;
    gap: 1em;
    flex-wrap: wrap;
    width: 12em;
}

.login__qrcode {
    height: 10em;
}

.login-box__title.center {
    text-align: center;
}

.login-separator {
    width: 3em;
}
